import React from 'react';

const Portfolio = () => {
  const projects = [
    {
      image: "https://images.unsplash.com/photo-1581094794329-c8112a89af12?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=2070&q=80",
      title: "Galpão Industrial",
      location: "São Paulo - SP",
      description: "Estrutura metálica de 2.000m² com cobertura em telhas metálicas",
      duration: "45 dias"
    },
    {
      image: "https://images.unsplash.com/photo-1504307651254-35680f356dfd?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=2073&q=80",
      title: "Caixa d'Água de Aço",
      location: "Campinas - SP",
      description: "Caixa d'água formato taça com capacidade para 50.000 litros",
      duration: "15 dias"
    },
    {
      image: "https://images.unsplash.com/photo-1558618666-fcd25c85cd64?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=2070&q=80",
      title: "Estrutura Metálica",
      location: "Santos - SP",
      description: "Estrutura para edifício comercial de 5 andares",
      duration: "60 dias"
    },
    {
      image: "https://images.unsplash.com/photo-1581094794329-c8112a89af12?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=2070&q=80",
      title: "Telhado Metálico",
      location: "Ribeirão Preto - SP",
      description: "Cobertura metálica para residência com 300m²",
      duration: "10 dias"
    },
    {
      image: "https://images.unsplash.com/photo-1504307651254-35680f356dfd?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=2073&q=80",
      title: "Escada Caracol",
      location: "São José dos Campos - SP",
      description: "Escada metálica caracol para acesso ao mezanino",
      duration: "7 dias"
    },
    {
      image: "https://images.unsplash.com/photo-1558618666-fcd25c85cd64?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=2070&q=80",
      title: "Tubulação Industrial",
      location: "Guarulhos - SP",
      description: "Rede de tubulações para sistema de combate a incêndio",
      duration: "20 dias"
    }
  ];

  const scrollToContact = () => {
    const element = document.getElementById('contact');
    if (element) {
      element.scrollIntoView({ behavior: 'smooth' });
    }
  };

  return (
    <section id="portfolio" className="bg-white section-padding">
      <div className="container-custom">
        <div className="text-center mb-16">
          <h2 className="text-4xl md:text-5xl font-heading font-bold text-servitec-dark mb-6">
            Portfólio / <span className="gradient-text">Obras Realizadas</span>
          </h2>
          <p className="text-xl text-gray-600 max-w-3xl mx-auto">
            Conheça alguns dos nossos projetos mais recentes e veja a qualidade 
            do nosso trabalho em cada obra entregue.
          </p>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8 mb-12">
          {projects.map((project, index) => (
            <div key={index} className="card group overflow-hidden">
              <div className="relative overflow-hidden rounded-xl mb-4">
                <img 
                  src={project.image}
                  alt={project.title}
                  className="w-full h-64 object-cover group-hover:scale-110 transition-transform duration-500"
                />
                <div className="absolute inset-0 bg-gradient-to-t from-servitec-dark/60 to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-300"></div>
                <div className="absolute bottom-4 left-4 right-4 text-white opacity-0 group-hover:opacity-100 transition-opacity duration-300">
                  <p className="text-sm font-medium">{project.description}</p>
                </div>
              </div>
              
              <div className="space-y-2">
                <h3 className="text-xl font-heading font-bold text-servitec-dark">
                  {project.title}
                </h3>
                
                <div className="flex items-center space-x-2 text-gray-600">
                  <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
                    <path fillRule="evenodd" d="M5.05 4.05a7 7 0 119.9 9.9L10 18.9l-4.95-4.95a7 7 0 010-9.9zM10 11a2 2 0 100-4 2 2 0 000 4z" clipRule="evenodd" />
                  </svg>
                  <span className="text-sm">{project.location}</span>
                </div>
                
                <div className="flex items-center space-x-2 text-gray-600">
                  <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
                    <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm1-12a1 1 0 10-2 0v4a1 1 0 00.293.707l2.828 2.829a1 1 0 101.415-1.415L11 9.586V6z" clipRule="evenodd" />
                  </svg>
                  <span className="text-sm">Prazo: {project.duration}</span>
                </div>
              </div>
            </div>
          ))}
        </div>

        {/* CTA Section */}
        <div className="bg-gradient-to-r from-servitec-blue-light to-servitec-blue-lighter rounded-2xl p-8 md:p-12 text-center text-white">
          <h3 className="text-3xl md:text-4xl font-heading font-bold mb-4">
            Gostou do nosso trabalho?
          </h3>
          <p className="text-xl mb-8 max-w-2xl mx-auto opacity-90">
            Fale agora pelo WhatsApp e solicite seu orçamento personalizado!
          </p>
          <button 
            onClick={scrollToContact}
            className="bg-servitec-orange hover:bg-orange-600 text-white font-bold py-4 px-8 rounded-xl text-lg transition-all duration-300 hover:scale-105 shadow-lg hover:shadow-xl"
          >
            Fale agora pelo WhatsApp!
          </button>
        </div>
      </div>
    </section>
  );
};

export default Portfolio;
